@use "../../sass/abstracts/" as *;

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  padding: 30px 20px;
  background: #ffffff;
  box-shadow: 0px 10px 50px rgba(0, 48, 92, 0.1);
  border-radius: 25px;
  &:last-child {
    margin-bottom: 0;
  }

  @include breakpoint(sm) {
    padding: 30px 30px 60px;
  }
  @include breakpoint(md) {
    padding: 30px 30px 60px;
  }
  @include breakpoint(lg) {
    padding: 30px 30px 60px;
  }

  .imgWrapper {
    margin-bottom: 1px;
  }

  img {
    width: 100%;
    height: 100%;
  }

  h3 {
    margin-bottom: 5px;

    @include breakpoint(md) {
      margin-bottom: 15px;
    }
  }

  &.main {
    @include breakpoint(md) {
      min-height: 428px;

      .imgWrapper {
        margin-bottom: 10px;
      }

      p {
        max-width: 305px;
      }
    }

    @include breakpoint(lg) {
      margin-bottom: 0;
      max-width: 229px;
      height: 416px;
      min-height: auto;

      .imgWrapper {
        margin-bottom: 15px;
        width: 170px;
        height: 170px;
      }
    }

    @include breakpoint(xl) {
      max-width: none;
      height: auto;
      padding-bottom: 60px;

      .imgWrapper {
        width: 230px;
        height: 230px;
      }

      p {
        max-width: 310px;
      }
    }
  }

  &.free {
    padding: 30px;

    .imgWrapper {
      margin-bottom: 10px;
    }

    h3 {
      margin-bottom: 15px;
    }

    @include breakpoint(md) {
      max-width: 210px;
      margin-bottom: 0;
      padding: 30px 10px;
    }

    @include breakpoint(lg) {
      min-width: 354px;
      padding-bottom: 60px;

      .imgWrapper {
        margin-bottom: 15px;
        width: 130px;
        height: 130px;
      }

      p {
        max-width: 60%;
        margin: 0 auto;
      }
    }

    @include breakpoint(xl) {
      min-width: auto;
      max-width: 270px;
      padding-bottom: 40px;

      p {
        max-width: 70%;
      }
    }
  }

  &.offer {
    img {
      margin-bottom: 20px;
    }

    h3 {
      margin-bottom: 15px;
    }

    @include breakpoint(md) {
      padding-bottom: 30px;
    }

    @include breakpoint(lg) {
      padding-top: 60px;
      padding-bottom: 60px;
      display: flex;
      flex-direction: row;
      gap: 40px;
      justify-content: center;
      margin-right: 15px;
      padding-left: 20px;

      .imgWrapper {
        width: 188px;
        height: 188px;
        flex-shrink: 0;
      }

      img {
        width: 100%;
        height: 100%;
      }

      h3,
      p {
        max-width: 240px;
        text-align: left;
      }

      &:nth-of-type(2) {
        &.offer {
          flex-direction: row-reverse;

          img {
            margin-bottom: 29px;
          }

          p {
            max-width: 250px;
          }
        }
      }
    }

    @include breakpoint(xl) {
      margin: 0;
      width: 50%;
      padding: 60px;
      gap: 0;
      justify-content: space-between;
    }
  }

  &:nth-of-type(1) {
    &.offer {
      @include breakpoint(xl) {
        flex-direction: row-reverse;
      }
    }
  }

  &.bonus {
    margin-bottom: 40px;
    box-shadow: none;
    padding: 0;

    .imgWrapper{
      margin-bottom: 7px;
    }

    h3 {
      margin-bottom: 15px;
    }

    p {
      @include breakpoint(md) {
        width: 100%;
        max-width: 300px;
      }

      @include breakpoint(xl) {
        width: 100%;
      }
    }

    @include breakpoint(lg) {
      margin: 0 auto;
      margin-bottom: 10px;
      max-width: 535px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      gap: 40px;

      @include breakpoint(xl) {
        gap: 30px;
      }

      h3,
      p {
        text-align: left;
        max-width: 325px;
      }

      .imgWrapper {
        width: 240px;
        height: 240px;
        flex-shrink: 0;
      }

      img {
        width: 100%;
        height: 100%;
      }

      &:nth-of-type(even) {
        flex-direction: row-reverse;
      }

      &:nth-of-type(4) {
        h3,
        p {
          max-width: 260px;
        }
      }
    }

    @include breakpoint(xl) {
      margin: 0;
      max-width: 525px;

      &:nth-of-type(even) {
        flex-direction: row;
      }

      .card {
        background: green;
      }
    }
  }
}
